import DatePicker from 'tui-date-picker'
import Grid from 'tui-grid'

const TUI_ROW_HEADER_CHECKBOX =
  '<input type="checkbox" name="_checked" style="margin-top: 3px; margin-left: 4px"/>'
const TUI_ROW_HEADER_CHECKBOX_WIDTH = 30
const TUI_ROW_HEIGHT = 28
const TUI_COLUMN_HEIGHT = 28

export default {
  createGridAll(gridId, isRowHeader, isCheck, columns, customMenu) {
    const headers = []
    if (isRowHeader | isCheck) {
      if (isRowHeader) {
        headers.push('rowNum')
      }
      if (isCheck) {
        headers.push({
          type: 'checkbox',
          header: TUI_ROW_HEADER_CHECKBOX,
          width: TUI_ROW_HEADER_CHECKBOX_WIDTH,
          minWidth: TUI_ROW_HEADER_CHECKBOX_WIDTH
        })
      }
    }

    let grid = new Grid({
      el: document.getElementById(gridId), // Container element
      usageStatistics: false,
      header: { height: TUI_COLUMN_HEIGHT, align: 'left' },
      rowHeight: TUI_ROW_HEIGHT,
      minRowHeight: TUI_ROW_HEIGHT,
      rowHeaders: headers,
      bodyHeight: 'fitToParent',
      columns: columns,
      columnOptions: {
        resizable: true
      },
      contextMenu: customMenu
      //   
      // contextMenu: ({ rowKey, columnName }) => [
      //   [
      //     {
      //       name: 'export',
      //       label: 'Export',
      //       subMenu: [
      //         {
      //           name: 'csvExport',
      //           label: 'CSV export',
      //           action: () => {
      //             grid.export('csv')
      //           }
      //         },
      //         {
      //           name: 'excelExport',
      //           label: 'Excel export',
      //           action: () => {
      //             grid.export('xlsx')
      //           }
      //         }
      //       ]
      //     },
      //     {
      //       name: 'open',
      //       label: '',
      //       action: () => {
      //         this.openQuery = true
      //         console.log(rowKey + '/' + columnName)
      //       }
      //     }
      //   ]
      // ]
    })
    Grid.setLanguage('ko')

    return grid
  },

  createGridNMenu(gridId, isRowHeader, columns, customMenu) {
    return this.createGridAll(gridId, isRowHeader, false, columns, customMenu)
  },

  createGridNCheck(gridId, isRowHeader, columns) {
    return this.createGridAll(gridId, isRowHeader, true, columns, '')
  },

  createGrid(gridId, isRowHeader, columns) {
    return this.createGridAll(gridId, isRowHeader, false, columns, '')
  },

  createRangeDatePicker() {
    let datePicker = new DatePicker.createRangePicker({
      startpicker: {
        date: new Date(),
        format: 'yyyy-MM-dd',
        input: '#startpicker-input',
        container: '#startpicker-container'
      },
      endpicker: {
        date: new Date(),
        format: 'yyyy-MM-dd',
        input: '#endpicker-input',
        container: '#endpicker-container'
      },
      language: 'ko',
      usageStatistics: false
    })

    return datePicker
  }
}
